.app{
  display: flex;
  flex-direction: column;
}
.navcontainer{
  display: flex;
  justify-content: flex-end;
}
nav ul {
  list-style-type: none;

}
ul li {
  margin: 0rem 1rem;
}
nav {
  background-color:  #007FFF;
  padding: 1rem;
  
}
.card {
  display: flex;
  width: 980px;
  margin: auto;
  margin-top: 2rem;
  gap: 3rem;
}
footer{
  padding: 4px 0px;
  width: 100%;
  height: 40px;
  text-align: center;
  background-color: hsl(200deg 6% 90%)
}
cardrightcontainer{
  text-align:left;
}
footer p {
  padding-bottom: 6px;
}
.header2{
  margin-top: -2rem;
  font-size: 27px;
}
a {
  color: white;
  text-decoration: none;
}
.section2{
  display: flex;
  width: 980px;
  margin: auto;
  margin-top: 4rem;
  gap: 3rem;
}
.workclass{
  width: 980px;
  margin: auto;
  font-size: 80px;
}
.section3{
  display: flex;
  width: 980px;
  margin: auto;
  margin-top: 4rem;
  gap: 3rem;
  margin-bottom: 3rem;
}
.section3 > div > div {
  margin-bottom:5.5rem;
}
.divider{
  border: 1px solid;
    height: 38rem;
    margin-top: 2rem;
}
.workclasswrapper{
  border-top: 2px solid black;
    border-bottom: 2px solid black;
    padding: 2rem 0rem;
    margin: 2rem 0rem;
}
.section3 > div > div h1 {
  font-size: 50px;
}
.section3 > div > div h3 {
  font-size: 16px;
}
.rightcontainer > div  h1 {
  font-size: 29px !important;
}
.section3 > .rightcontainer > div {
  margin-bottom:9.5rem;
}
p{
  font-size: 18px;
}